@import url('https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@300;400;500;600;700&family=Open+Sans:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@200&display=swap');

:root {
  --font-base: 'Roboto Mono', monospace;
  --font-alt: 'Open Sans', sans-serif;
  
  --color-white: #ecfffe;
  --color-lightgolden: #D9FFFC;
  --color-golden: #66fcf1;
  --color-grey: #45a29e;
  --color-darkgrey : #285757;
  --color-black: #0b0c10;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

a {
  color: unset;
  text-decoration: none;
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: slide-bottom 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
            transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}




::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #45a29e;
}

::-webkit-scrollbar-thumb {
  background: #0b0c10;
  border-radius: 5px;
}

/* Customize scroll behavior (smooth scrolling) */
html {
  scroll-behavior: smooth;
}