.app__experience {
    background: var(--color-black);
    align-items: center;
    display: flex;
    flex-direction: column;
}

.app__Experience-title{
    margin: 2rem 0;;
    text-align: center;
}

.app__experience-element{
    width: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}


.app__experience-elementCard_title h1,
.app__experience-elementCard_title p {
    font-family: var(--font-alt);
  font-weight: 400;
  /* letter-spacing: 0.04em; */
  line-height: 2rem;
  color: var(--color-black);
  font-size: 1.5em;
}

.app__experience-elementCard_title p {
    color: var(--color-white);
  font-size: 1.2rem;
}

.app__experience-elementCard_date {
    color: var(--color-lightgolden);
    font-family: var(--font-alt);
}

.app__experience-elementCard_jobPoints {
    color: var(--color-black);
    font-family: var(--font-alt);
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

.app__Experience-company_icon{
    object-fit: contain;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    align-items: center;
}

@media screen and (max-width: 2000px) {
}

@media screen and (max-width: 900px) {

    .app__experience-elementCard_jobPoints {
        font-size: 0.9rem;
    }
    .app__experience-elementCard_date {
        color: var(--color-black);
    }
}

@media screen and (max-width: 650px) {
