.app__laurels {
    background: var(--color-black);
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    align-items: center;
}



.app__laurels-technologies{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 5rem;
    margin: 5rem 2rem;
    text-align: center;
    align-items: center;
}

.app__laurels_info {
    margin: 0 0;
    padding: 0 0;
    height: 5rem;
}

.app__laurels-technologies_card {
    justify-content: center;
    text-align: center;
    top: 0;
    align-items: center;
}

.app__laurels-technologies_card img{
    height: 5rem;
    width: 5rem;
    border-radius: 2px;
}