.app__aboutus {
    position: relative;
    background: var(--color-black);
    height: '100vh';
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 300px;
    height: 400px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
    height: 900px;
}

.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    margin: 2rem 0;
    color: var(--color-white);
}

.app__aboutus-content_knife-line {
    border-left: 1px solid black; /* Adjust the color and thickness as needed */
    height: 25rem; /* Adjust the height of the line as needed */
    width: 1px;
    background: var(--color-golden);
  }

@media screen and (max-width: 2000px) {
    .app__aboutus-content_knife img {
        height: 1110px;
    }

    .app__aboutus-content_history p,
    .app__aboutus-content_about p {
    margin: 4rem 0;
    color: var(--color-white);
}
}

@media screen and (max-width: 900px) {

    .app__aboutus{
        height: 10rem;
    }

    .app__aboutus-content {
        flex-direction: column;
        height: 50%;
    }

    .app__aboutus-content_knife {
        margin: 0;
    }

    .app__aboutus-content_knife-line {
        display: none;
      }
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }
}