.app__popup {
  z-index: 1000;
  background-color: var(--color-darkgrey);
  position: fixed;
  top: 50%;
  /* Center vertically */
  left: 50%;
  /* Center horizontally */
  transform: translate(-50%, -50%);
  /* Center the popup */
  border-radius: 2rem;
  text-align: center;
  width: 60%;
  max-width: 60%;
  padding: 2rem;
  /* Add padding for spacing inside */
  border: 2px solid var(--color-white);
}

.app__popup-content {
  text-align: center;
}

.app__popup-content_description {
  font-family: var(--font-alt);
  color: var(--color-white);
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

.app__popup h2 {
  font-size: 2rem;
  padding-bottom: 2rem;
}

.app__popup p {
  /* font-size: 1.5rem; */
  line-height: 1.5;
}

.app__popup-projectLink {
  bottom: 1rem;
}

.app__popup-closeButton {
  position: absolute;
  color: var(--color-golden);
  top: 1rem;
  right: 1rem;
  cursor: pointer;
}

.app__popup-closeButton:hover {
  color: var(--color-white);
}

.app__popup-overlay {
  z-index: 999;
  /* Make sure the overlay is below the popup */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent background color */
  transition: 0.3s ease;
}


@media screen and (max-width: 1150px) {
  .app__popup h2 {
    font-size: 1.8rem;
  }

  .app__popup-content_description {
    font-family: var(--font-alt);
    color: var(--color-white);
    margin-bottom: 2rem;
    font-size: 1rem;
  }
}

@media screen and (max-width: 650px) {
  .app__popup h2 {
    font-size: 1.5rem;
  }
}